import { handleActions } from 'redux-actions'

import {TodoState} from '../../constants/models'
// import {
//     CREATE_TODO,
//     DELETE_TODO,
//     CHANGE_TEXT,
// } from '../../constants/actionTypes'

export default handleActions({
    CREATE_TODO: (prev_state, { payload }) => {
        let todos = prev_state.get('todos').push(prev_state.get('todo'))
        return prev_state.set('todos', todos)
    },
    DELETE_TODO: (prev_state, { payload }) => {
        let remain_todos = prev_state.get('todos').splice(payload.index, 1)
        return prev_state.set('todos', remain_todos)
    },
    CHANGE_TEXT: (prev_state, { payload }) => {
        let updated_todo = prev_state.get('todo').merge(payload)
        return prev_state.set('todo', updated_todo)
    },
}, TodoState)



