import React from 'react'

const TodoHeader = ({
    onChangeText,
    onCreateTodo,
    todo,
}) => {
    return (
        <div>
            <h1>Todo App</h1>
            <input type="text" value={todo.get('text')} onChange={onChangeText} />
            <button onClick={onCreateTodo}>ADD</button>
        </div>
    )
}

export default TodoHeader


